import React, { useEffect, useState } from "react";
import { CircularProgress, Typography } from "@material-ui/core";

const Loader = props => {
  const [display, setDisplay] = useState("flex");
  const [title, setTitle] = useState("Cargando....");
  // When Update Component
  useEffect(() => {
    props.show ? setDisplay("flex") : setDisplay("none");
    props.title ? setTitle(title) : setTitle("Cargando....");
  }, [display, title, props]);

  return (
    <div className="loader" style={{ display }}>
      <CircularProgress variant="indeterminate" color="primary" />
      <Typography variant="caption" style={{ marginLeft: 20 }}>
        {title || props.title}
      </Typography>
    </div>
  );
};

export default React.memo(Loader, (prevProps, nextProps) => {
  return prevProps === nextProps;
});
