import React, { Component } from "react";
import {
  Grid,
  TextField,
  Button,
  Card,
  CardContent,
  CardActions,
  Icon,
  withStyles,
  Typography
} from "@material-ui/core";
import { Loader, AvatarPreview, ChangePassword } from "../componets";
import request from "../helpers/request";
import { api } from "../config";

const Styles = ({ spacing }) => {
  return {
    buttonTopContainer: {
      marginLeft: spacing.unit
    }
  };
};

class UserForm extends Component {
  params = this.props.navigation.getParams();
  state = {
    showPreview: false,
    values: {},
    loading: false,
    imageFile: null,
    angle: 0,
    avatarUri: "",
    openChangePassword: false
  };

  imageBase64 = null;

  componentDidMount() {
    document.body.style.background = "#e1e1e1";
    this.OnResize();
    window.addEventListener("resize", () => this.OnResize());
    this.setState({ values: this.params.user });
  }

  componentWillUnmount() {
    document.body.style.background = "#ffffff";
    window.removeEventListener("resize", this.OnResize);
  }

  OnResize() {
    const viewport = window.innerWidth;
    if (viewport > 768) {
      this.props.navigation.setNavbarParams({
        title: "Profiles",
        parentView: this.params.parentView,
        child: true
      });
    } else {
      this.props.navigation.setNavbarParams({ visible: false });
    }
  }

  avatarChange = ev => {
    const img = ev.target.files[0];
    const that = this;
    if (
      img.type.includes("jpeg") ||
      img.type.includes("jpg") ||
      img.type.includes("png")
    ) {
      const image = new Image();
      image.src = URL.createObjectURL(img);
      image.onload = function() {
        that.setState({ showPreview: true, imageFile: img });
      };
    } else {
      return alert("No es una imagen o no es un formato soportado (jpg o png)");
    }
  };

  handleChange = el => {
    const { values } = this.state;
    values[el.target.getAttribute("name")] = el.target.value;
    this.setState(() => ({ values }));
  };

  handleSubmit = async () => {
    const body = {
      data: this.state.values,
      avatar: this.imageBase64,
      id: this.state.values._id
    };
    delete body.data._id;
    await request.Put("/users/", body);
    this.props.navigation.go(this.params.parentView);
  };

  render() {
    const { imageFile, showPreview, loading, openChangePassword } = this.state;
    return (
      <Grid container>
        <Loader show={loading} />
        <AvatarPreview
          open={showPreview}
          imageFile={imageFile}
          id="preview-img"
          onSave={url => {
            this.imageBase64 = url;
            this.refs.avatar.src = url;
            this.setState({ showPreview: false });
          }}
        />

        <ChangePassword
          open={openChangePassword}
          onClose={() => this.setState({ openChangePassword: false })}
          onSave={async password => {
            await request.Put("/users/update-password", {
              id: this.params.user._id,
              password
            });

            this.setState({ openChangePassword: false });
          }}
        />

        <Grid item xs={12}>
          <div className="user-profile-banner" id="user-profile-banner">
            <div className="user-profile-banner-bar">
              <Icon
                style={{ color: "#ffffff" }}
                onClick={() => this.props.navigation.go(this.params.parentView)}
              >
                arrow_back
              </Icon>
              <input
                type="file"
                name="picture"
                ref="file"
                style={{ display: "none" }}
                accept="image/*"
                capture="camera"
                onChange={this.avatarChange}
              />
              <div>
                <Icon
                  style={{ color: "#ffffff" }}
                  onClick={() => this.setState({ openChangePassword: true })}
                >
                  restore
                </Icon>
                <Icon
                  style={{ color: "#ffffff", marginLeft: ".5rem" }}
                  onClick={() => this.refs.file.click()}
                >
                  camera_alt
                </Icon>
              </div>
            </div>
            <img
              id="avatar"
              ref="avatar"
              onClick={() => this.refs.file.click()}
              src={api + "/get-avatar/" + this.params.user._id}
              alt={this.state.values.name || "profile"}
            />
          </div>
        </Grid>

        <Grid item xs={12}>
          <Card>
            <CardContent>
              <Typography>
                Usuario
                <br />
                {this.state.values.username || ""}
              </Typography>

              <TextField
                label="Nombre"
                fullWidth
                value={this.state.values.name || ""}
                name="name"
                onChange={this.handleChange}
                InputLabelProps={{
                  shrink: this.state.values.name ? true : false
                }}
              />
              <TextField
                label="Celular"
                fullWidth
                value={this.state.values.cellphone || ""}
                name="cellphone"
                onChange={this.handleChange}
                type="text"
              />
              <TextField
                label="Correo"
                fullWidth
                value={this.state.values.email || ""}
                onChange={this.handleChange}
                name="cellphone"
              />
            </CardContent>
            <CardActions>
              <Button
                variant="contained"
                color="primary"
                onClick={this.handleSubmit}
              >
                Guardar
              </Button>
            </CardActions>
          </Card>
        </Grid>
      </Grid>
    );
  }
}

export default withStyles(Styles)(UserForm);
