import React from "react";
import { TextField } from "@material-ui/core";

export default ({ inputs = [], onChange = () => null, values = {} }) => {
  return (
    <div>
      {inputs.map((input, i) => {
        return (
          <TextField
            key={i + "__" + input.key}
            value={values[input.key] || ""}
            type={values[input.type || "text"]}
            label={input.label || "Input"}
            onChange={({ target }) => onChange(input.key, target.value, i)}
            fullWidth
            required={input.required || false}
            tabIndex={1}
            autoComplete={input.autoComplete || "off"}
          />
        );
      })}
    </div>
  );
};
