import React, { Component } from "react";
import {
  Avatar,
  Typography,
  Grid,
  Card,
  CardContent,
  CardHeader,
  TextField,
  Icon
} from "@material-ui/core";
import { api, host } from "../config";
import request from "../helpers/request";
import io from "socket.io-client";

const socket = io(host + "/tlacrm", {
  query: { room: "budgets" }
});

const Comment = props => {
  return (
    <Card>
      <CardHeader
        avatar={<Avatar src={props.avatar} />}
        title={props.username}
        subheader={props.createDate}
      />
      <CardContent>
        {props.comment.split("\n").map((el, i) => {
          return <Typography key={el + i}>{el}</Typography>;
        })}
      </CardContent>
    </Card>
  );
};

const RenderComments = props => {
  if (props.comments.length > 0) {
    const date = d => {
      if (d === undefined) return "";
      return new Date(d).toLocaleString("es-MX");
    };
    return props.comments
      .map((el, i) => {
        return (
          <Comment
            key={el.username + i}
            username={el.username}
            comment={el.comment}
            createDate={date(el.createDate)}
            avatar={api + "/get-avatar/" + (el.userId || 1)}
          />
        );
      })
      .reverse();
  } else {
    return <div />;
  }
};

const CommentInput = props => {
  let stopTyping = setTimeout(() => socket.emit("client_stop_typing"), 3000);
  const onChange = () => {
    clearTimeout(stopTyping);
    socket.emit("client_typing", { user: props.username });
    stopTyping = setTimeout(() => socket.emit("client_stop_typing"), 3000);
  };

  const addComment = () => {
    const val = document.querySelector("#comment-input").value;
    if (val === "") return alert("No debes dejar el comentario en blanco");

    socket.emit("add_commnet", {
      id: props.budgetId,
      comment: {
        comment: val,
        username: props.username,
        userId: props.id
      }
    });
    document.querySelector("#comment-input").value = "";
  };

  return (
    <div className="add-comment-input">
      <TextField
        defaultValue={props.value}
        label="Agrega un comentario"
        onChange={onChange}
        id="comment-input"
      />
      <Icon style={{ marginRight: 10, cursor: "pointer" }} onClick={addComment}>
        send
      </Icon>
    </div>
  );
};

export default class BudgetComments extends Component {
  store = window.localStorage;
  user = {};
  handleTyping = "";
  state = {
    typing: "",
    comment: "",
    comments: [],
    clientName: "",
    budgetDescription: "",
    createDate: "",
    typingState: ""
  };

  budget = this.props.navigation.getParams().budget;

  async componentDidMount() {
    this.props.navigation.setNavbarParams({
      child: true,
      title: "Prospectos",
      parentView: "Budgets"
    });

    document.body.style.background = "#e1e1e1";
    this.socketEvents();
    const response = await request.Get("/budgets/find-one/" + this.budget._id);
    if (response.error) return alert(response.message);
    this.setState({
      comments: response.budget.comments || [],
      clientName: response.budget.clientName,
      budgetDescription: response.budget.budgetDescription,
      createDate: response.budget.createDate
    });
  }

  componentWillMount() {
    if (this.store.getItem("ev-token") === null) this.store = sessionStorage;
    this.store = window.localStorage;
    this.user = JSON.parse(this.store.getItem("user"));
  }

  socketEvents() {
    socket.on("client_typing", () => {
      console.log("hi")
      if (this.state.typingState === "") {
        this.setState({ typingState: `estan escribiendo un comentario` });
      }
    });
    socket.on("client_stop_typing", () => this.setState({ typingState: "" }));
    socket.on("add_comment", comment => {
      console.log(comment);
      const { comments } = this.state;
      comments.push(comment);
      this.setState({ comments });
    });
  }

  addComment() {
    const { budget, comment } = this.state;
    const id = budget._id;
    budget.comments.push({
      comment,
      username: this.user.name
    });
    this.setState({ budget, comment: "" });

    delete budget._id;

    this.socket.emit("add_comment", { id, budget });
  }

  render() {
    const {
      comment,
      comments,
      clientName,
      createDate,
      typingState,
      budgetDescription
    } = this.state;

    return (
      <Grid container style={{ marginTop: "4rem" }}>
        <Grid item sm={12} xs={6}>
          <Card style={{ width: "100vw" }}>
            <CardHeader
              avatar={<Avatar>{clientName[0]}</Avatar>}
              title={clientName}
              subheader={createDate.replace("T", " ")}
            />
            <CardContent>
              {budgetDescription.split("\n").map((el, i) => {
                return <Typography key={el + i}>{el}</Typography>;
              })}
            </CardContent>
          </Card>
        </Grid>
        <Grid item sm={12} xl={6} style={{ marginTop: "1rem" }}>
          <Card style={{ width: "100vw" }}>
            <CardHeader
              avatar={<Avatar src={api + "/get-avatar/" + this.user._id} />}
              title={this.user.displayName}
              subheader={
                <CommentInput
                  value={comment}
                  username={this.user.displayName}
                  id={this.user._id}
                  budgetId={this.budget._id}
                />
              }
            />
            <CardContent>
              <Grid item sm={12} xs={6}>
                <Typography variant="caption">{typingState}</Typography>
              </Grid>
              <RenderComments comments={comments} />
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    );
  }
}
