import './foregroundDetection';
export default async () => {
    if ('serviceWorker' in navigator) {
        window.reg = await navigator.serviceWorker.register('sw.js', { scope: '/tlacrm/' })
            .catch(error => console.log(`Error when try to register service worker, ${error}`));
    } else {
        console.log('Service Worker is not supported')
    }

}