import React from 'react';
import { List, ListItem, ListItemAvatar, ListItemText, Typography, Avatar } from '@material-ui/core';
export default props => {
    let date = props.date ? new Date(props.date) : new Date();
    date = date.toLocaleString('es-MX');
    return (
        <List>
            <ListItem>
                <ListItemAvatar>
                    <Avatar>{props.clientName[0]}</Avatar>
                </ListItemAvatar>
                <ListItemText secondary={date}>
                    <Typography>{props.clientName}</Typography>
                </ListItemText>
            </ListItem>
        </List>
    )
}