import React, { Component } from "react";
import {
  Card,
  CardHeader,
  CardContent,
  TextField,
  CardActions,
  Switch,
  Button,
  FormControlLabel,
  Grid
} from "@material-ui/core";
import request from "../helpers/request";
import { Loader } from "../componets";

class Login extends Component {
  state = {
    show: false,
    username: "",
    password: "",
    autoLogin: false
  };

  login = async ev => {
    ev.preventDefault();
    this.setState({ show: true });
    const { username, password, autoLogin } = this.state;
    const response = await request.login(username, password, autoLogin);
    this.setState({ show: false });
    if (response.error) return alert(response.message);
    window.location.reload();
  };

  render() {
    const { show, username, password } = this.state;

    return (
      <Grid
        container
        justify="center"
        alignItems="center"
        style={{ height: "100vh" }}
      >
        <Loader show={show} />
        <form onSubmit={this.login}>
          <div className="bg-login" />
          <Card style={{ width: "90%", margin: "auto" }}>
            <CardHeader />
            <CardContent>
              <TextField
                label="Usuario"
                fullWidth
                value={username || ""}
                onChange={ev => this.setState({ username: ev.target.value })}
                tabIndex={1}
              />
              <TextField
                label="Contraseña"
                type="password"
                fullWidth
                value={password || ""}
                onChange={ev => this.setState({ password: ev.target.value })}
                tabIndex={2}
              />
            </CardContent>
            <CardActions>
              <Grid container>
                <Grid item xs={3} />
                <Grid item xs={6}>
                  <FormControlLabel
                    control={
                      <Switch
                        value={true}
                        color="primary"
                        onChange={ev =>
                          this.setState({ autoLogin: ev.target.checked })
                        }
                      />
                    }
                    label="Auto Login"
                    tabIndex={3}
                  />
                </Grid>
                <Grid item xs={3} />
                <Grid item xs={12}>
                  <Button
                    fullWidth
                    variant="contained"
                    color="primary"
                    type="submit"
                  >
                    Login
                  </Button>
                </Grid>
              </Grid>
            </CardActions>
          </Card>
        </form>
      </Grid>
    );
  }
}

export default Login;
