import React, { Component } from "react";
import { Grid, Switch, FormControlLabel, Button } from "@material-ui/core";
import { Loader, MsgError, InputGroup } from "../componets";
import request from "../helpers/request";
import inputs from "../elements-components/inputs_of_clients";

export default class ClientForm extends Component {
  params = this.props.navigation.getParams();

  state = {
    values: {},
    loader: true,
    fetchError: {
      status: false,
      message: ""
    }
  };

  componentDidMount() {
    const { navigation } = this.props;
    navigation.setNavbarParams({
      child: true,
      parentView: "Clients",
      title: "Datos Clientes"
    });
    if (navigation.getParams().action === "update") {
      this.setState({ loader: false, values: this.params.client });
    } else {
      this.setState({ loader: false });
    }
  }

  handleSubmit(ev) {
    ev.preventDefault();
    if (this.props.navigation.getParams().action === "update") {
      this.updateClient();
    } else {
      this.addClient();
    }
  }

  async updateClient() {
    try {
      const { values } = this.state;
      const body = {
        id: values._id,
        data: values
      };
      delete body.data._id;
      const response = await request.Put("/clients", body);
      if (response.error) return alert(response.message);
      this.props.navigation.go("Clients");
    } catch (error) {
      console.log(error);
    }
  }

  async addClient() {
    try {
      const { values } = this.state;
      const response = await request.Post("/clients", { client: values });
      if (response.error) return alert(response.message);
      this.props.navigation.go("Clients");
    } catch (error) {
      console.log(error);
    }
  }

  handleChange = (key, value) => {
    const { values } = this.state;
    values[key] = value;
    this.setState({ values });
  };

  render() {
    return (
      <div className="container padding-1 margin-1">
        {this.state.fetchError.status ? (
          <MsgError message={this.state.fetchError.message} />
        ) : (
          <form onSubmit={ev => this.handleSubmit(ev)}>
            <Grid container spacing={16}>
              <Loader show={this.state.loader} />
              <Grid item xs={12}>
                <InputGroup
                  values={this.state.values}
                  inputs={inputs.baseInputs}
                  onChange={this.handleChange}
                />
              </Grid>

              <Grid item xs={12}>
                <FormControlLabel
                  control={
                    <Switch
                      checked={this.state.values.invoiceInfo || false}
                      color="default"
                      value="invoiceInfo"
                      onChange={({ target }) =>
                        this.handleChange("invoiceInfo", target.checked)
                      }
                    />
                  }
                  label="Datos de factura"
                  tabIndex={1}
                />
              </Grid>

              <Grid item xs={12}>
                {this.state.values.invoiceInfo && (
                  <InputGroup
                    values={this.state.values}
                    inputs={inputs.invoices}
                    onChange={this.handleChange}
                  />
                )}
              </Grid>

              <Grid xs={12} item>
                <Button
                  variant="contained"
                  color="inherit"
                  fullWidth
                  type="submit"
                >
                  Enviar
                </Button>
              </Grid>
            </Grid>
          </form>
        )}
      </div>
    );
  }
}
