import Cryptr from "cryptr";
import { api, publicVapidKey, secret, host } from "../config";
const cryptr = new Cryptr(secret);

let store = localStorage;
if (sessionStorage.getItem("isLogginIn") === "true") store = sessionStorage;

const headers = {
  method: "get",
  headers: {
    "Content-Type": "application/json",
    Accept: "application/json",
    token: store.getItem("token")
  }
};

// Function for make url base
const urlBase64ToUint8Array = () => {
  const padding = "=".repeat((4 - (publicVapidKey.length % 4)) % 4);
  const base64 = (publicVapidKey + padding).replace(/_/g, "/");
  const rawData = window.atob(base64);
  return Uint8Array.from([...rawData].map(char => char.charCodeAt(0)));
};

// Push subscription handler
async function getPushSubscription() {
  const applicationServerKey = urlBase64ToUint8Array();
  const url = host + "/webpush/subscribe";
  const sub = await window.reg.pushManager
    .subscribe({ userVisibleOnly: true, applicationServerKey })
    .catch(error => console.log(`Error to subscribe to push manager ${error}`));

  headers.body = JSON.stringify(sub);
  headers.method = "post";

  await fetch(url, headers);

  window.location.reload();
}

// Make request

const makeRequest = async url => {
  try {
    const request = await fetch(`${api}${url}`, headers);
    const response = await request.json();
    return response;
  } catch ({ message }) {
    return { error: true, message: "Servidor fuera de linea" };
  }
};

export default {
  subscribeNotificacion: function(response) {
    if (!response.error) {
      Notification.requestPermission().then(async permission => {
        if (permission === "granted") {
          await getPushSubscription();
        }
      });
    }
  },

  login: async function(username, password, autoLogin) {
    const request = await fetch(`${api}/users/login`, {
      method: "post",
      body: JSON.stringify({ username, password, autoLogin }),
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      }
    });

    if (!request.ok) return { error: true, message: request.statusText };

    const response = await request.json();
    if (response.error) return response;
    const userModel = JSON.stringify({
      autoLogin,
      _id: response.user._id,
      avatar: response.user.avatar,
      profile: response.user.profile,
      displayName: response.user.name,
      username: response.user.username,
      password: autoLogin ? cryptr.encrypt(password) : ""
    });

    autoLogin ? (store = localStorage) : (store = sessionStorage);

    store.setItem("token", response.token);
    store.setItem("user", userModel);
    store.setItem("isLoggedIn", true);
    store.setItem("current-view", "Home");

    this.subscribeNotificacion(response);
    return response;
  },

  autoLogin: async function() {
    const user = JSON.parse(store.getItem("user"));
    if (!user.autoLogin)
      return { error: true, message: "No se puede regenerar el token" };
    const username = user.username;
    const password = cryptr.decrypt(user.password);

    const request = await fetch(api + "/users/login", {
      method: "post",
      headers: {
        "Content-Type": "Application/Json",
        Accept: "Application/Json"
      },
      body: JSON.stringify({ username, password, autoLogin: true  })
    });

    if (request.status >= 400) return console.log(request.statusText);

    const response = await request.json();
    if (response.error) return response;
    store.setItem("token", response.token);
    return response.token;
  },

  Upload: async function(url, file) {
    headers.headers["Content-Type"] = "multipart/form-data";
    headers.method = "post";
    headers.body = file;
    let response = await makeRequest(url);
    if (response.tokenExpired) {
      headers.headers.token = await this.autoLogin();
      response = await makeRequest(url);
    }

    return response;
  },

  Get: async function(url) {
    headers.method = "get";
    if (headers.body) delete headers.body;
    let response = await makeRequest(url);

    if (response.tokenExpired) {
      const responseToken = await this.autoLogin();
      console.log(responseToken);
      if (responseToken.error) {
        return responseToken;
      } else {
        headers.headers.token = responseToken;
        response = await makeRequest(url);
      }
    }

    return response;
  },
  Put: async function(url, body) {
    headers.method = "put";
    headers.body = JSON.stringify(body);
    let response = await makeRequest(url);
    if (response.tokenExpired) {
      const responseToken = await this.autoLogin();
      if (responseToken.error) {
        return responseToken;
      } else {
        headers.headers.token = responseToken;
        response = await makeRequest(url);
      }
    }
    return response;
  },
  Post: async function(url, body) {
    headers.method = "post";
    headers.body = JSON.stringify(body);
    let response = await makeRequest(url);
    if (response.tokenExpired) {
      const responseToken = await this.autoLogin();
      if (responseToken.error) {
        return responseToken;
      } else {
        headers.headers.token = responseToken;
        response = await makeRequest(url);
      }
    }
    return response;
  },
  Delete: async function(url) {
    headers.method = "delete";
    if (headers.body) delete headers.body;
    let response = await makeRequest(url);
    if (response.tokenExpired) {
      const responseToken = await this.autoLogin();
      if (responseToken.error) {
        return responseToken;
      } else {
        headers.headers.token = responseToken;
        response = await makeRequest(url);
      }
    }
    return response;
  }
};
