import React from "react";
import {
  Dialog,
  DialogContent,
  DialogActions,
  Button
} from "@material-ui/core";

class AvatarPreview extends React.Component {
  state = {
    open: false,
    imageFile: null
  };

  position = 1;
  canvas = null;
  ctx = null;
  img = null;

  componentWillReceiveProps(props) {
    if (props !== null) {
      this.setState({ open: props.open, imageFile: props.imageFile });
    }
  }

  canvasRef = ref => {
    if (ref !== null) {
      this.canvas = ref;
      this.ctx = this.canvas.getContext("2d");
      this.renderImagePreview();
    }
  };

  renderImagePreview() {
    if (this.canvas !== null) {
      const image = document.querySelector("#pic");
      image.src = window.URL.createObjectURL(this.state.imageFile);
      image.onload = () => {
        this.img = image;
        this.ctx = this.canvas.getContext("2d");
        this.ctx.drawImage(image, 0, 0, this.canvas.width, this.canvas.height);
      };
    }
  }

  rotate = () => {
    const w = this.canvas.width;
    const h = this.canvas.height;
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    this.ctx.save();
    switch (this.position) {
      case 0:
        this.ctx.drawImage(this.img, 0, 0, w, h);
        this.position = 1;
        break;
      case 1:
        this.ctx.translate(w / 2, h / 2);
        this.ctx.rotate((90 * Math.PI) / 180);
        this.ctx.translate(-(h / 2), -(w / 2));
        this.ctx.drawImage(this.img, 0, 0, h, w);
        this.position = 2;
        break;
      case 2:
        this.ctx.translate(w / 2, h / 2);
        this.ctx.rotate((180 * Math.PI) / 180);
        this.ctx.translate(-(w / 2), -(h / 2));
        this.ctx.drawImage(this.img, 0, 0, w, h);
        this.position = 3;
        break;
      case 3:
        this.ctx.translate(w / 2, h / 2);
        this.ctx.rotate((270 * Math.PI) / 180);
        this.ctx.translate(-(h / 2), -(w / 2));
        this.ctx.drawImage(this.img, 0, 0, h, w);
        this.position = 0;
        break;
      default:
        alert("nothing to di");
    }
    this.ctx.restore();
  };

  handleClose = () => this.setState({ open: false });

  render() {
    const { open } = this.state;
    return (
      <Dialog
        open={open}
        onClose={this.handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        style={{ height: "60vh", width: "80wh" }}
      >
        <DialogContent itemRef="dialog-el">
          <img
            id="pic"
            alt="pc"
            width="800"
            height="400"
            style={{ display: "none" }}
          />
          <canvas
            ref={this.canvasRef}
            id={this.props.id}
            style={{ width: "100%", height: "100%" }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={this.rotate}>Rotar</Button>
          <Button
            onClick={() => {
              const url = this.canvas.toDataURL();
              this.props.onSave(url);
            }}
          >
            Guardar
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}

export default AvatarPreview;
