import React from "react";
import {
  Card,
  Avatar,
  CardContent,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Typography,
  CardActions,
  Button,
  TextField,
  FormControlLabel,
  Switch,
  Grid
} from "@material-ui/core";
import request from "../helpers/request";

export default props => {
  const [value, setValue] = React.useState(
    props.budget.budgetDescription || ""
  );
  const [edith, setEdith] = React.useState(false);
  const [visited, setVisited] = React.useState(props.budget.visited || false);

  const CardActionMain = () => {
    return (
      <CardActions className="card-action__center">
        <Button color="default" disabled={visited}>
          {visited ? "Visitado" : "Sin Visitar"}
        </Button>
        <Button
          color="primary"
          onClick={() => {
            props.navigation.go("BudgetComments", { budget: props.budget });
          }}
        >
          Comentarios
        </Button>
        <Button color="primary" onClick={() => setEdith(true)}>
          Editar
        </Button>
      </CardActions>
    );
  };

  const CardActionEdith = () => {
    return (
      <CardActions className="card-action__right">
        <Button
          variant="contained"
          color="primary"
          onClick={async () => {
            await request.Put("/budgets/update", {
              id: props.budget._id,
              data: {
                budgetDescription: value,
                visited
              }
            });
            setEdith(false);
          }}
        >
          Guardar
        </Button>
      </CardActions>
    );
  };

  const RenderCardAction = () => {
    if (edith) {
      return <CardActionEdith />;
    } else {
      return <CardActionMain />;
    }
  };

  return (
    <Card>
      <CardContent>
        <List>
          <ListItem>
            <ListItemAvatar>
              <Avatar>{props.budget.clientName[0]}</Avatar>
            </ListItemAvatar>
            <ListItemText
              secondary={new Date(props.budget.createDate).toLocaleString(
                "es-MX"
              )}
            >
              <Typography paragraph>{props.budget.clientName}</Typography>
            </ListItemText>
          </ListItem>
          <ListItem>
            <ListItemText>
              {edith ? (
                <div>
                  <TextField
                    label="Editar información del presupuesto"
                    value={value}
                    rows="4"
                    variant="outlined"
                    multiline
                    fullWidth
                    onChange={({ target }) => setValue(target.value)}
                  />
                  <FormControlLabel
                    label="Visitado"
                    control={
                      <Switch
                        value={"visited"}
                        onChange={({ target }) => setVisited(target.checked)}
                        color="primary"
                        checked={visited}
                      />
                    }
                  />
                </div>
              ) : (
                <Grid container>
                  <Grid item xs={12}>
                    <Typography paragraph>
                      {value.split("\n").map((item, i) => {
                        return (
                          <span key={item + i}>
                            {item} <br />
                          </span>
                        );
                      })}
                    </Typography>
                  </Grid>
                </Grid>
              )}
            </ListItemText>
          </ListItem>
        </List>
      </CardContent>
      <RenderCardAction />
    </Card>
  );
};
