import React, { Component } from "react";
import {
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Icon,
  TextField,
  Grid
} from "@material-ui/core";
import request from "../helpers/request";
import { Loader } from "../componets";

export default class SelectClient extends Component {
  state = {
    clients: [],
    show: true
  };

  params = this.props.navigation.getParams();
  searching = null;

  componentDidMount() {
    this.props.navigation.setNavbarParams({
      title: "Selecciona un cliente",
      child: true,
      parentView: this.params.parentView || "Clients"
    });
    this.fetchClients();
  }

  async fetchClients() {
    const response = await request.Get("/clients/fetch/50/0");
    if (response.error) return alert(response.message);
    this.setState({ clients: response.clients, show: false });
  }

  searchClient(value) {
    clearTimeout(this.searching);
    this.searching = setTimeout(async () => {
      this.setState({ show: true });
      const response = await request.Get("/clients/search/" + value);
      if (response.error) return alert(response.message);
      this.setState({ clients: response.clients, show: false });
    }, 1000);
  }

  render() {
    return (
      <div className="container">
        <Loader show={this.state.show} />

        <Grid container spacing={16}>
          <Grid item xs={12} style={{ padding: "1rem" }}>
            <TextField
              variant="standard"
              type="search"
              label="Buscar Cliente"
              fullWidth
              onChange={({ target }) => {
                target.value === ""
                  ? this.fetchClients()
                  : this.searchClient(target.value);
              }}
            />
          </Grid>
          <Grid item xs={12}>
            <List>
              {this.state.clients.map(client => {
                return (
                  <ListItem
                    key={client._id}
                    onClick={() => {
                      this.props.navigation.go(this.params.childView, client);
                    }}
                  >
                    <ListItemText secondary={client.cellphone || ""}>
                      {client.name}
                    </ListItemText>
                    <ListItemIcon>
                      <Icon>next</Icon>
                    </ListItemIcon>
                  </ListItem>
                );
              })}
            </List>
          </Grid>
        </Grid>
      </div>
    );
  }
}
