import React, { Component } from "react";
import {
  Grid,
  Card,
  Icon,
  Slide,
  Button,
  Switch,
  AppBar,
  Toolbar,
  Dialog,
  TextField,
  CardHeader,
  Typography,
  CardContent,
  FormControlLabel
} from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";

const useStyles = makeStyles({
  appBar: {
    position: "relative"
  },
  title: {
    marginLeft: "1rem",
    flex: 1
  },
  padding: {
    padding: "1rem"
  },
  description: {
    height: "4rem",
    marginTop: "1rem"
  }
});

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const Calculos = ({ subtotal, iva, total, anticipo }) => {
  return (
    <div>
      <Grid container>
        <Grid item xs={5} />
        <Grid item xs={3}>
          <Typography align="right">Subtotal</Typography>
        </Grid>
        <Grid item xs={1} />
        <Grid item xs={3}>
          ${parseFloat(subtotal).toLocaleString("esMX")}
        </Grid>
      </Grid>
      <Grid container>
        <Grid item xs={5} />
        <Grid item xs={3}>
          <Typography align="right">Iva</Typography>
        </Grid>
        <Grid item xs={1} />
        <Grid item xs={3}>
          ${parseFloat(iva).toLocaleString("esMX")}
        </Grid>
      </Grid>
      <Grid container>
        <Grid item xs={5} />
        <Grid item xs={3}>
          <Typography align="right">Anticipo</Typography>
        </Grid>
        <Grid item xs={1} />
        <Grid item xs={3}>
          ${parseFloat(anticipo).toLocaleString("esMX")}
        </Grid>
      </Grid>
      <Grid container>
        <Grid item xs={5} />
        <Grid item xs={3}>
          <Typography align="right">Total</Typography>
        </Grid>
        <Grid item xs={1} />
        <Grid item xs={3}>
          ${(parseFloat(total) - parseFloat(anticipo)).toLocaleString("esMX")}
        </Grid>
      </Grid>
    </div>
  );
};

const AddJobDialog = () => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  function handleClickOpen() {
    setOpen(true);
  }

  function handleClose() {
    setOpen(false);
  }

  return (
    <div>
      <Button color="inherit" onClick={handleClickOpen}>
        Nuevo
      </Button>
      <Dialog fullScreen open={open} onClose={handleClose}>
        <div>
          <Icon onClick={handleClose}>close</Icon>
        </div>
        <div className={classes.padding}>
          <TextField label="Trabajo" fullWidth tabIndex={1} type="text" />
          <TextField label="Material" fullWidth tabIndex={1} type="text" />
          <TextField
            label="Cantidad"
            type="number"
            fullWidth
            tabIndex={1}
          />
          <TextField label="Precio" fullWidth tabIndex={1} />
          <TextField
            label="Descripcion"
            multiline
            rowsMax={5}
            fullWidth
            tabIndex={1}
            variant="outlined"
            className={classes.description}
          />
        </div>
      </Dialog>
    </div>
  );
};

const ListJobsDialog = () => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  function handleClickOpen() {
    setOpen(true);
  }

  function handleClose() {
    setOpen(false);
  }

  return (
    <div>
      <Button
        variant="outlined"
        color="primary"
        fullWidth
        onClick={handleClickOpen}
      >
        Agregar Trabajo
      </Button>
      <Dialog
        fullScreen
        open={open}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        <AppBar className={classes.appBar} color="default">
          <Toolbar>
            <Icon onClick={handleClose}>close</Icon>
            <Typography variant="h6" className={classes.title}>
              Agregar Trabajo
            </Typography>
            <AddJobDialog />
          </Toolbar>
        </AppBar>
      </Dialog>
    </div>
  );
};

class AddJob extends Component {
  params = {};
  state = {
    values: {
      iva: 0,
      jobs: [],
      total: 0,
      tax: false,
      anticipo: 0,
      subtotal: 0,
      payments: []
    }
  };
  componentDidMount() {
    const { setNavbarParams, getParams } = this.props.navigation;
    this.params = getParams();
    setNavbarParams({
      title: "Agregar Trabajo",
      child: true,
      parentView: "SelectClient",
      params: {
        parentView: "Jobs",
        childView: "AddJob"
      }
    });
    this.calculateTax();
  }

  calculateTax() {
    const { values } = this.state;
    if (values.tax) {
      const tax = parseFloat(values.subtotal) * 0.16;
      this.handleChange("iva", tax);
    } else {
      this.handleChange("iva", 0);
    }
  }

  handleChange = (key, value) => {
    const { values } = this.state;
    values[key] = value;
    this.setState({ values });
  };

  render() {
    const { values } = this.state;
    return (
      <div className="container">
        <Grid item xs={12}>
          <Card>
            <CardHeader
              title={this.params.name || ""}
              subheader={this.params.cellphone || ""}
            />
            <CardContent>
              <Grid container>
                <Grid item xs={12}>
                  <TextField
                    label="Anticipo"
                    type="number"
                    fullWidth
                    tabIndex={1}
                    onChange={({ target }) =>
                      this.handleChange("anticipo", target.value)
                    }
                  />
                </Grid>
                <Grid item xs={12} style={{ marginTop: 20 }}>
                  <ListJobsDialog />
                </Grid>
                <Grid item xs={12} style={{ marginTop: 20 }}>
                  <FormControlLabel
                    control={
                      <Switch
                        checked={values.tax || false}
                        color="default"
                        value="tax"
                        onChange={({ target }) => {
                          this.handleChange("tax", target.checked);
                          this.calculateTax();
                        }}
                      />
                    }
                    label="Calcular IVA"
                    tabIndex={1}
                  />
                </Grid>
                <Grid item xs={12} style={{ marginTop: 20 }}>
                  <Calculos
                    subtotal={values.subtotal}
                    iva={values.iva}
                    total={values.total}
                    anticipo={values.anticipo}
                  />
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
      </div>
    );
  }
}

export default AddJob;
