import React, { Component, useEffect } from "react";

import { Navbar } from "./componets";

import Login from "./views/Login";

import Clients from "./views/Clients";
import ClientForm from "./views/ClientForm";

import Budgets from "./views/Budgets";
import AddBudget from "./views/AddBudget";
import BudgetComments from "./views/BudgetComments";
import SelectClient from "./views/SelectClient";

import Users from "./views/Users";
import UserForm from "./views/UserForm";
import Jobs from "./views/Jobs";
import AddJob from "./views/AddJob";

function Home(props) {
  useEffect(() => {
    props.navigation.setNavbarParams({
      type: "parent",
      title: "Inicio"
    });
  });
  return <div>Home</div>;
}

class App extends Component {
  state = {
    views: {
      Jobs,
      Home,
      Login,
      Users,
      AddJob,
      Budgets,
      Clients,
      UserForm,
      AddBudget,
      ClientForm,
      SelectClient,
      BudgetComments
    },
    currentView: "UserForm",
    params: {},
    loggedIn: false,
    isMobile: false,
    navbarParams: {}
  };

  store = localStorage;

  componentWillMount() {
    this.breakPoints();

    if (this.store.getItem("isLoggedIn") === null) this.store = sessionStorage;

    if (this.store.getItem("isLoggedIn") === "true") {
      this.setState({
        loggedIn: true,
        currentView: this.store.getItem("current-view")
      });
    }
  }

  breakPoints() {
    const viewport = window.innerWidth;
    if (/Mobile/.test(navigator.userAgent)) {
      this.setState({ isMobile: true });
    } else {
      if (viewport > 768) this.setState({ isMobile: true });
    }
  }

  render() {
    const { views, currentView, isMobile } = this.state;
    const View = views[currentView];

    const navigation = {
      go: (currentView, params = {}) => {
        this.store.setItem("current-view", currentView);
        this.store.setItem("params", JSON.stringify(params));

        this.setState({ currentView, params });
      },
      setNavbarParams: navbarParams => this.setState({ navbarParams }),
      isMobile,
      getParams: () => {
        let params = {};
        localStorage.getItem("params")
          ? (params = localStorage.getItem("params"))
          : (params = sessionStorage.getItem("params"));

        return JSON.parse(params);
      }
    };

    return !this.state.loggedIn ? (
      <Login />
    ) : (
      <div>
        <Navbar navigation={navigation} params={this.state.navbarParams} />
        <View navigation={navigation} />
      </div>
    );
  }
}

export default App;
