import React, { Component } from "react";
import { Grid } from "@material-ui/core";
import request from "../helpers/request";
import { CardBudget, AddButton, Loader } from "../componets";

export default class Budgets extends Component {
  state = {
    budgets: [],
    show: true,
    errorMessage: ""
  };

  componentDidMount() {
    this.props.navigation.setNavbarParams({
      type: "parent",
      title: "Presupuestos",
      placeholder: "Buscar Presupuesto",
      onSearch: val => {
        this.setState({ loading: true });
        clearTimeout(this.watcher);
        this.watcher = setTimeout(async () => {
          let response;
          if (val === "") {
            response = await request.Get("/budgets/fetch/50/0");
          } else {
            response = await request.Get("/budgets/search/" + val);
          }
          if (response.error)
            return this.setState({
              loading: false,
              messageError: response.message
            });
          this.setState({ loading: false, budgets: response.budgets });
        }, 500);
      }
    });

    this.fetchBudgets();
  }

  async fetchBudgets() {
    const response = await request.Get("/budgets/fetch/50/0");
    if (response.error) {
      this.setState({ show: false, errorMessage: response.messange });
    } else {
      this.setState({ budgets: response.budgets, show: false });
    }
  }

  render() {
    return (
      <div className="container" style={{ padding: ".2rem" }}>
        <Grid container spacing={16} style={{ marginBottom: "3rem" }}>
          <Loader show={this.state.show} />
          {this.state.budgets
            .map(budget => {
              return (
                <Grid item xs={12} key={budget._id}>
                  <CardBudget
                    budget={budget}
                    navigation={this.props.navigation}
                  />
                </Grid>
              );
            })
            .reverse()}
        </Grid>
        <AddButton
          onClick={() =>
            this.props.navigation.go("SelectClient", {
              parentView: "Budgets",
              childView: "AddBudget"
            })
          }
        />
      </div>
    );
  }
}
