import React, { Component } from "react";
import {
  List,
  ListItem,
  ListItemAvatar,
  Avatar,
  Typography,
  ListItemText,
  Grid
} from "@material-ui/core";
import { Loader, MsgError } from "../componets";
import request from "../helpers/request";
import { api } from "../config";

export default class Jobs extends Component {
  state = {
    users: [],
    error: {
      status: false,
      message: ""
    },
    loader: true
  };

  async componentDidMount() {
    this.props.navigation.setNavbarParams({
      type: "parent",
      title: "Usuarios",
      moreMenu: true,
      placeholder: "Buscar Cliente"
    });

    const response = await request.Get("/users/fetch");
    if (response === undefined)
      return this.setState({
        error: {
          status: true,
          message: "Ocurrio un error con el servidor"
        },
        loader: false
      });
    if (response.error) {
      this.setState({
        error: { status: true, message: response.message },
        loader: false
      });
    } else {
      this.setState({
        loader: false,
        users: response.users
      });
    }
  }

  render() {
    const { loader, error, users } = this.state;
    return (
      <Grid container className="container" style={{ minHeight: "70vh" }}>
        <Grid xs="auto" md={2} item />
        <Grid item xs={12} md={8}>
          {error.status ? (
            <MsgError message={error.message} />
          ) : (
            <List>
              {users.map(user => {
                return (
                  <ListItem
                    key={user._id}
                    onClick={() =>
                      this.props.navigation.go("UserForm", {
                        parentView: "Users",
                        user: user,
                        action: "update"
                      })
                    }
                  >
                    <ListItemAvatar>
                      <Avatar src={api + "/get-avatar/" + user._id} />
                    </ListItemAvatar>
                    <ListItemText secondary={user.username}>
                      <Typography>{user.name}</Typography>
                    </ListItemText>
                  </ListItem>
                );
              })}
            </List>
          )}
        </Grid>
        <Loader show={loader} />
      </Grid>
    );
  }
}
