import React from "react";

const style = {
    container: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        width: "100%",
        height: "100%",
        position: "fixed"
    },
    msg: {
        color: "grey"
    }
}

export default (props) => {

    return (
        <div style={style.container}>
            <h5 style={style.msg}>{props.message}</h5>
        </div>
    )

}