import React from "react";
import {
  Grid,
  Card,
  CardHeader,
  CardContent,
  Typography,
  CardActions,
  Button
} from "@material-ui/core";
import request from "../helpers/request";
import { MsgError, Loader, AddButton } from "../componets";

const RowHeader = () => {
  return (
    <Grid container>
      <Grid item xs={5}>
        Descripcion
      </Grid>
      <Grid item xs={3}>
        Cantidad
      </Grid>
      <Grid item xs={2}>
        Precio
      </Grid>
      <Grid item xs={2}>
        Subtotal
      </Grid>
    </Grid>
  );
};

const Row = props => {
  return props.jobs.map((job, i) => {
    return (
      <Grid container key={i}>
        <Grid item xs={5}>
          <Typography variant="caption">{job.trabajo}</Typography>
        </Grid>
        <Grid item xs={3}>
          <Typography variant="caption">{job.cantidad}</Typography>
        </Grid>
        <Grid item xs={2}>
          <Typography variant="caption">
            ${parseFloat(job.precio).toLocaleString("esMX")}
          </Typography>
        </Grid>
        <Grid item xs={2}>
          <Typography variant="caption">
            $
            {(parseFloat(job.cantidad) * parseFloat(job.precio)).toLocaleString(
              "esMX"
            )}
          </Typography>
        </Grid>
      </Grid>
    );
  });
};

const Calculos = ({ job }) => {
  return (
    <div>
      <Grid container>
        <Grid item xs={5} />
        <Grid item xs={3}>
          <Typography align="right">Subtotal</Typography>
        </Grid>
        <Grid item xs={1} />
        <Grid item xs={3}>
          ${parseFloat(job.subtotal).toLocaleString("esMX")}
        </Grid>
      </Grid>
      <Grid container>
        <Grid item xs={5} />
        <Grid item xs={3}>
          <Typography align="right">Iva</Typography>
        </Grid>
        <Grid item xs={1} />
        <Grid item xs={3}>
          ${parseFloat(job.iva).toLocaleString("esMX")}
        </Grid>
      </Grid>
      <Grid container>
        <Grid item xs={5} />
        <Grid item xs={3}>
          <Typography align="right">Total</Typography>
        </Grid>
        <Grid item xs={1} />
        <Grid item xs={3}>
          ${parseFloat(job.total).toLocaleString("esMX")}
        </Grid>
      </Grid>
    </div>
  );
};

const CardJob = props => {
  return props.jobs.map(job => {
    return (
      <Grid key={job._id} item xs={12} style={{ marginTop: 5 }}>
        <Card>
          <CardHeader title={job.client.name} subheader={job.create_date} />
          <CardContent>
            <RowHeader />
            <Row jobs={job.jobs} />
            <Calculos job={job} />
          </CardContent>
          <CardActions>
            <Grid container>
              <Grid item xs={9}>
                <Typography variant="caption">
                  {job.tax && "El documento contiene iva"}
                </Typography>
              </Grid>
              <Grid item xs={3}>
                <Button color="secondary">Detalle</Button>
              </Grid>
            </Grid>
          </CardActions>
        </Card>
      </Grid>
    );
  });
};

class Jobs extends React.Component {
  state = {
    jobs: [],
    errorMsg: "",
    loading: true
  };

  componentDidMount() {
    this.props.navigation.setNavbarParams({
      type: "parent",
      title: "Trabajos",
      placeholder: "Buscar Trabajos",
      onSearch: () => null
      // onRefresh: () => this.fetchClients()
    });
    this.fetchJobs();
  }

  fetchJobs = async () => {
    const response = await request.Get("/jobs/fetch/1");
    console.log(response);
    if (response.error) {
      this.setState({ errorMsg: response.message, loading: false });
    } else {
      this.setState({ jobs: response.jobs, loading: false });
    }
  };

  render() {
    const { jobs, errorMsg, loading } = this.state;
    return (
      <Grid container className="container">
        <CardJob jobs={jobs} />
        <Loader show={loading} />
        <AddButton
          onClick={() =>
            this.props.navigation.go("SelectClient", {
              parentView: "Jobs",
              childView: "AddJob"
            })
          }
        />
        {errorMsg !== "" && <MsgError msg={errorMsg} />}
      </Grid>
    );
  }
}

export default Jobs;
