import React, { Component } from "react";
import {
  Icon,
  List,
  Avatar,
  AppBar,
  Divider,
  Toolbar,
  ListItem,
  Typography,
  withStyles,
  ListItemText,
  ListItemAvatar,
  SwipeableDrawer
} from "@material-ui/core";
import { fade } from "@material-ui/core/styles/colorManipulator";
import { api } from "../config";
import AppBarC from "./AppBar";

let store = localStorage;
if (store.getItem("user") === null) store = sessionStorage;

const styles = theme => ({
  root: {
    width: "100%"
  },
  grow: {
    flexGrow: 1
  },
  menuButton: {
    marginRight: 20
  },
  title: {
    display: "none",
    [theme.breakpoints.up("sm")]: {
      display: "block"
    }
  },
  search: {
    position: "relative",
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.white, 0.25)
    },
    marginRight: theme.spacing.unit * 2,
    marginLeft: 0,
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing.unit * 3,
      width: "auto"
    }
  },
  searchIcon: {
    width: theme.spacing.unit * 9,
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  inputRoot: {
    color: "inherit",
    width: "100%"
  },
  inputInput: {
    paddingTop: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    paddingLeft: theme.spacing.unit * 10,
    transition: theme.transitions.create("width"),
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: 200
    }
  },
  sectionDesktop: {
    display: "none",
    [theme.breakpoints.up("md")]: {
      display: "flex"
    }
  },
  sectionMobile: {
    display: "flex",
    [theme.breakpoints.up("md")]: {
      display: "none"
    }
  }
});

const logout = toggleDrawer => {
  let _logout = window.confirm("¿Deseas cerrar sesión?");
  if (_logout) {
    store.clear();
    window.location.reload();
  } else {
    toggleDrawer(false);
  }
};

class MainBar extends Component {
  user = JSON.parse(store.getItem("user"));
  state = {
    toggleDrawer: false
  };
  render() {
    const { toggleDrawer } = this.state;
    const { params, navigation } = this.props;
    return (
      <div className="navbar-container">
        <AppBarC
          onDrawerIconClick={() => this.setState({ toggleDrawer: true })}
          placeholder={params.placeholder}
          onSearch={e => params.onSearch(e.target.value)}
        />
        <SwipeableDrawer
          open={toggleDrawer}
          onClose={() => this.setState({ toggleDrawer: false })}
          onOpen={() => this.setState({ toggleDrawer: true })}
        >
          <div className="w-drawer">
            <List>
              <ListItem>
                <ListItemAvatar>
                  <Avatar src={api + "/get-avatar/" + this.user._id} />
                </ListItemAvatar>
                <ListItemText>{this.user.name || ""}</ListItemText>
              </ListItem>
            </List>

            <Divider />

            <List>
              <ListItem
                onClick={() => {
                  this.setState({ toggleDrawer: false });
                  navigation.go("Users");
                }}
              >
                Usuarios
              </ListItem>
              {this.user.profile.modules.map(item => {
                return (
                  <ListItem
                    onClick={() => {
                      this.setState({ toggleDrawer: false });
                      navigation.go(item.reactPath);
                    }}
                    key={item.key}
                  >
                    {item.display}
                  </ListItem>
                );
              })}
              <ListItem
                onClick={() =>
                  logout(() => this.setState({ toggleDrawer: true }))
                }
              >
                Cerrar Session
              </ListItem>
            </List>
          </div>
        </SwipeableDrawer>
      </div>
    );
  }
}

const ChildBar = props => {
  const { title, parentView } = props.params;
  const { navigation } = props;

  return (
    <div className="navbar-container" style={{ marginBottom: "2rem" }}>
      <AppBar position="fixed" color="default">
        <Toolbar className="navbar" variant="regular">
          <Icon
            className={props.classes.menuButton}
            onClick={() => navigation.go(parentView)}
          >
            arrow_back
          </Icon>
          <Typography variant="h6" color="inherit">
            {title}
          </Typography>
          <div />
        </Toolbar>
      </AppBar>
    </div>
  );
};

const Navbar = props => {
  if (props.params.visible === undefined) props.params.visible = true;
  if (!props.params.visible) return <div />;

  if (props.params.type === "parent") {
    return <MainBar {...props} />;
  } else {
    return <ChildBar {...props} />;
  }
};

export default withStyles(styles)(Navbar);
