import React, { Component } from "react";
import { Fab, Icon } from "@material-ui/core";
import { UserBar } from "../componets";
import request from "../helpers/request";

export default class AddBudgets extends Component {
  state = {
    budgets: [],
    values: {}
  };

  componentDidMount() {
    const { setNavbarParams, getParams } = this.props.navigation;
    setNavbarParams({
      title: "Agregar Presupuestos",
      child: true,
      parentView: "SelectClient",
      params: {
        parentView: "Budgets",
        childView: "AddBudget"
      }
    });

    this.setState({
      values: {
        clientId: getParams()._id,
        clientName: getParams().name,
        clientCellphone: getParams().cellphone,
        budgetDescription: ""
      }
    });
  }

  render() {
    const { values } = this.state;
    return (
      <div className="container">
        <UserBar clientName={values.clientName || ""} />
        <textarea
          className="add-budget"
          rows="4"
          placeholder="Agregar Información del presupuesto"
          required
          value={values.budgetDescription}
          onChange={({ target }) => {
            values.budgetDescription = target.value;
            this.setState({ values });
          }}
          ref="textbox"
        />

        <div className="fab-fixed">
          <Fab
            ev-target="add"
            color="primary"
            onClick={async () => {
              if (values.budgetDescription !== "") {
                const response = await request.Post("/budgets/save", {
                  budget: values
                });
                if (response.error) return alert(response.message);
                this.props.navigation.go("Budgets");
              } else {
                alert("Debe de llenar la información solicitada");
              }
            }}
          >
            <Icon>check</Icon>
          </Fab>
        </div>
      </div>
    );
  }
}
