import React from "react";
import { Paper, InputBase, Divider, Icon } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";

const useStyles = makeStyles({
  root: {
    padding: "2px 4px",
    display: "flex",
    alignItems: "center",
    width: "100%",
    height: "3.5rem"
  },
  input: {
    marginLeft: 8,
    flex: 1
  },
  divider: {
    width: 1,
    height: 28,
    margin: 4
  }
});

export default props => {
  const classes = useStyles();
  return (
    <Paper className={classes.root}>
      <Icon onClick={props.onDrawerIconClick}>menu</Icon>
      <InputBase
        className={classes.input}
        placeholder={props.placeholder || "Buscar"}
        onChange={(ev) => props.onSearch(ev)}
        inputProps={{ "aria-label": "Search Google Maps" }}
      />
      <Icon>search</Icon>
      <Divider className={classes.divider} />
      <Icon>more_vert</Icon>
    </Paper>
  );
};
