import React from "react";
import {
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Icon,
  Dialog,
  DialogTitle,
  DialogContent,
  Typography,
  ListItemAvatar,
  Avatar
} from "@material-ui/core";

import { Loader, MsgError, AddButton } from "../componets";
import request from "../helpers/request";

const PhoneDialog = ({ client = {}, onClose, open = false }) => {
  const makePhoneCall = phone => window.open(`tel:${phone}`);

  return (
    <Dialog open={open} onClose={onClose}>
      <DialogTitle>Elige que numero marcar</DialogTitle>
      <DialogContent>
        <Typography onClick={() => makePhoneCall(client.cellphone || "")}>
          {client.cellphone || ""}
        </Typography>
        {client.phone && (
          <Typography
            onClick={() => makePhoneCall(client.phone || "")}
            style={{ marginTop: 10 }}
          >
            {client.phone || ""}
          </Typography>
        )}
      </DialogContent>
    </Dialog>
  );
};

class Clients extends React.Component {
  watcher = null;
  navigation = this.props.navigation;

  state = {
    clients: [],
    loading: true,
    messageError: "",
    openPhoneDialog: false,
    clientSelected: {}
  };

  componentDidMount() {
    this.navigation.setNavbarParams({
      type: "parent",
      moreMenu: true,
      placeholder: "Buscar Clientes",
      onSearch: val => {
        this.setState({ loading: true });
        clearTimeout(this.watcher);
        this.watcher = setTimeout(async () => {
          let response;
          if (val === "") {
            response = await request.Get("/clients/fetch/50/0");
          } else {
            response = await request.Get("/clients/search/" + val);
          }
          if (response.error)
            return this.setState({
              loading: false,
              messageError: response.message
            });
          this.setState({ loading: false, clients: response.clients });
        }, 500);
      }
    });

    this.fetchClients();
  }

  async fetchClients() {
    const response = await request.Get("/clients/fetch/50/0");
    if (response.error)
      return this.setState({ loading: false, messageError: response.message });
    this.setState({ clients: response.clients, loading: false });
  }

  render() {
    const {
      loading,
      clients,
      messageError,
      openPhoneDialog,
      clientSelected
    } = this.state;

    return (
      <div className="container">
        <Loader show={loading} />

        <PhoneDialog
          open={openPhoneDialog}
          onClose={() => {
            this.setState({ openPhoneDialog: false });
          }}
          client={clientSelected}
        />

        {clients.length > 0 ? (
          <List>
            {clients.map(client => {
              return (
                <ListItem
                  key={client._id}
                  onClick={() => {
                    this.navigation.go("ClientForm", {
                      action: "update",
                      client
                    });
                  }}
                >
                <ListItemAvatar>
                  <Avatar>{client.name[0]}</Avatar>
                </ListItemAvatar>
                  <ListItemText secondary={client.cellphone}>
                    {client.name}
                  </ListItemText>
                  <ListItemSecondaryAction>
                    <Icon
                      color="action"
                      onClick={() =>
                        this.setState({
                          openPhoneDialog: true,
                          clientSelected: client
                        })
                      }
                    >
                      phone
                    </Icon>
                  </ListItemSecondaryAction>
                </ListItem>
              );
            })}
          </List>
        ) : (
          <MsgError message={messageError} />
        )}

        <AddButton
          onClick={() => this.navigation.go("ClientForm", { action: "add" })}
        />
      </div>
    );
  }
}

export default Clients;
